   <td>{{item.sm_name}}</td>\n          <td>\n            <img :src=\"item.quality_certificate\"\n                 ref='images'>\n          </td>\n          <td>\n            <img :src=\"item.quality_undertaking\"\n                 ref=\"images\">\n          </td>\n          <td>\n            <img :src=\"item.quality_inspection_report\"\n                 ref=\"images\">\n          </td>\n          <td>\n            {{item.my_retention_money}}\n          </td>\n          <td>\n            {{item.set_retention_money}}\n          </td>\n          <td>\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"detailList=item.detail,detailVisible=true,detail()\">详情</el-button>\n\n          </td>\n        </tr>\n      </table>\n      <el-dialog title=\"详情\"\n                 center\n                 :visible.sync=\"detailVisible\">\n        <table class=\"table\">\n          <tr>\n            <th>油品名称</th>\n            <th>总后台的油品名称</th>\n            <th>油品介绍</th>\n            <th>升数</th>\n            <th>结算价</th>\n            <th>型号</th>\n            <th>油品图片</th>\n          </tr>\n          <tr v-for='(item,index) in detailList'\n              :key=\"index\">\n            <td>\n              {{item.oil_name}}\n            </td>\n            <td>\n              {{item.little_name}}\n            </td>\n            <td>\n              <el-popover placement=\"top-start\"\n                          title=\"油品介绍\"\n                          width=\"200\"\n                          trigger=\"hover\"\n                          :content=\"item.about\">\n                <el-button slot=\"reference\"\n                           type=\"text\"\n                           class=\"ellipsis\"\n                           style=\"width:100px\">{{item.about}}</el-button>\n              </el-popover>\n            </td>\n            <td>\n              {{item.rise}}\n            </td>\n            <td>\n              {{item.price}}\n            </td>\n            <td>\n              {{item.standard}}\n            </td>\n            <td>\n              <img :src=\"item.cover\"\n                   ref=\"images\">\n            </td>\n          </tr>\n        </table>\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      authList: [],\n      list: [],\n      seCurId: '',\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\n      detailList: {},\n      page: 0,  //供应商列表当前页数\n      pageCount: 0, //供应商列表总页数\n      detailVisible: false,\n      sm_name: ''\n    }\n  },\n\n  methods: {\n    paging (e) {  //获取供应商列表当前页\n      this.page = e\n      this.init()\n    },\n    search () {  //搜索\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/SmList/getOilIndexList', { token: this.token, page: this.page, sm_name: this.sm_name })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n        if (res.data.code == 1) {\n          this.$nextTick(() => {\n            const ViewerRef = this.$refs.images\n            Viewer(ViewerRef)\n          })\n        } else {\n          this.$message({ message: res.data.msg, type: 'warning ' })\n        }\n\n      } catch (err) {\n        throw (err)\n      }\n    },\n    detail () {  //油品详情\n      this.$nextTick(() => {\n        const ViewerRef = this.$refs.images\n        Viewer(ViewerRef)\n      })\n    },\n\n    // 权限列表,当前所在权限页  \n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data || [];\n          this.authList = arr;\n\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  created () {\n    this.erAuth()\n  },\n  mounted () {\n\n    this.init() //初始化数据  \n\n  },\n}\n</script>\n<style scoped>\n.search {\n  width: 100%;\n  overflow: hidden;\n}\n.search {\n  padding: 0px 0px 20px 50px;\n}\n.search >>> .el-input {\n  width: 20%;\n}\n.search >>> button {\n  padding: 11px 15px;\n}\n\n.table img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n.ellipsis {\n  width: 80px !important;\n  white-space: nowrap !important;\n  text-overflow: ellipsis !important;\n  overflow: hidden !important;\n}\n</style>"],"sourceRoot":""}]);

